.container{
    height: 200px;
    width: 50%;
    /* border: 3px solid white; */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    position: absolute;
    top: 20%;
    left: 23%;
    /* background-color: white; */
    opacity: 80%;
}
.input{
    width: 50%;
    padding: 10px;
    border: 5px solid black;
    border-radius: 10px;
    color: black;
}
.inputContainer{
    width: 100%;
    text-align: center;
}
.messageContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.button{
    border: none;
    background-color: black;
    color: white;
    padding: 10px;
    border-radius: 10px;
    font-weight: bold;
    transition: all 0.3s ease-out;
}
.button:hover{
    cursor: pointer;
}
.button:active{
    opacity: 0.7;
    padding: 15px;
}

.buttonContainer{
    padding-top: 20px;
}
.text{
    color: white;
}
@media screen and (max-width: 770px){
    .container{
        width: 90%;
        left: 5%;
        top: 30%;
    }
    .input{
        width: 90%;
        padding: 20px;
        border: 5px solid black;
        border-radius: 10px;
        color: black;
        font-size: 20px;
        /* font-size: large; */
    }
    .input::placeholder{
        font-size: 20px;
        text-align: center;
    }
    .button{
        padding: 20px;
        font-size: 20px;
    }
}
